.StudentList{
	font-family: Montserrat;
}

.StudentListItem{
	padding: 10px;
	padding-top: 15px;
	padding-bottom: 15px;
	width: 100%;
	background-color: #fff;
	margin: 2px 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.StudentListItem:hover {
    background-color: #f5f5f6;
	cursor: pointer;

}

.profile-pic{
	border-radius: 50%;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	border: 1px solid #DDD;
	width: 40px;
	height: 40px;
	margin-right: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin-top: auto;
	margin-bottom: auto;
}

.list-item-details{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin-left: 10px;
	font-size: 12px;
}

.list-item-title{
	font-size: 14px;
	font-weight: 500;
	margin: 0;
	margin-bottom: 5px;
}

.list-item-details .context{
	font-weight: 300;
	color: var(--ic-brand-font-color-dark-lightened-30);
}

.list-item-details .highlight{
	font-weight: 500
}


