.ListTabHeader{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	font-family: Montserrat;
	padding: 5px;
}

.tab-link{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	background-color: var(--ic-brand-white);
	font-size: 14px;
	color: var(--ic-link-color);
	text-decoration: none;
	text-align: center;
	-ms-flex: 0 1 50%;
	    flex: 0 1 50%;
	padding: 15px;
	cursor: pointer;
	margin-left: 10px;
	margin-right: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-bottom: 2px solid transparent;
}

.tab-link:hover,
.tab-link.active{
	border-bottom: 2px solid var(--ic-link-color);	
}

.tab-link.active{
	color: #222;
}


